import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import SalesReturnUpdateForm from "./updateForm";
import SalesReturnDocUpdateForm from "./updateDocForm";
import HistorySalesReturn from "./history";

import notification from "../../../../components/notification";
import { Spin, Tooltip, Tabs } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/sales/salesReturn/actions";
import { Box } from "../../../App/app.style";
import Print from "./print";

const redirecTo = "/dashboard/sales/sales-returns";
const TabPane = Tabs.TabPane;

class UpdateSalesReturn extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editSalesReturn } = this.props;
    const { id } = this.props.match.params;
    editSalesReturn(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.salesReturn.error) {
      notification("error", this.props.salesReturn.error);
      this.props.clearError();
    }
    if (this.props.salesReturn.success) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.salesReturn.id_sales_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("success", this.props.salesReturn.success);
      this.props.clearSuccess();
    }
    if (this.props.salesReturn.warning) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.salesReturn.id_sales_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("warning", this.props.salesReturn.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updateSalesReturn } = this.props;
    const { id } = this.props.match.params;
    updateSalesReturn(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelSalesReturn } = this.props;
    const { id } = this.props.match.params;

    cancelSalesReturn(id, value);
  };

  render() {
    // console.log(this.props);
    const { 
      loading,
      loadingSubmit,
      salesReturn,
      printData
    } = this.props.salesReturn;
    const modified = { ...salesReturn, ...printData }

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="salesReturns.update.title" />}
            marginBottom={true}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={modified} />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistorySalesReturn
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={salesReturn ? salesReturn.description : null}
          />

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="jobCostings.tab.detail" />} key="1">
              {loading && !loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <SalesReturnUpdateForm
                  submit={this.handleSubmit}
                  salesReturn={this.props.salesReturn}
                  handleSubmitCancel={this.handleSubmitCancel}
                />
              )}
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostings.tab.attachment" />}
              key="2"
            >
              {loadingSubmit ? (
                <div className="spinStyle">
                  <Spin size="large" />
                </div>
              ) : (
                <SalesReturnDocUpdateForm
                  submit={this.handleSubmit}
                  salesReturn={this.props.salesReturn}
                  params={this.props.match.params}
                />
              )}
            </TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    salesReturn: state.salesReturn,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdateSalesReturn);
